import { mute } from './mute'
import { api, mtproto } from './api'
import { sleep } from '../sleep'

async function getUser() {
    return await api.call('users.getFullUser', {
        id: { _: 'inputUserSelf' }
    })
}

function sendCode(phone) {
    return api.call('auth.sendCode', {
        phone_number: phone,
        settings: { _: 'codeSettings' }
    })
}

function signIn({ code, phone, phone_code_hash }) {
    return api.call('auth.signIn', {
        phone_code: code,
        phone_number: phone,
        phone_code_hash: phone_code_hash
    })
}

function signUp({ phone, phone_code_hash }) {
    return api.call('auth.signUp', {
        phone_number: phone,
        phone_code_hash: phone_code_hash,
        first_name: 'MTProto',
        last_name: 'Core'
    })
}

function getPassword() {
    return api.call('account.getPassword')
}

function checkPassword({ srp_id, A, M1 }) {
    return api.call('auth.checkPassword', {
        password: { _: 'inputCheckPasswordSRP', srp_id, A, M1, }
    })
}





export const auth = {
    checkAuth: async () => {

        // ===================================================== //
        if (mute.mode) {
            await sleep(mute.delay)
            if (mute.is_auth) return { ok: true, user: { users: [{ first_name: 'first_name' }] } }
            else return { ok: false, message: 'not logined' }
        }
        // ===================================================== //

        try {
            const user = await getUser()
            return { ok: true, user: user }
        } catch (error) {
            return { ok: false, message: error.error_message }
        }
    },

    sendCode: async (phone) => {

        // ===================================================== //
        if (mute.mode) {
            if (mute.log) console.log({ phone })
            await sleep(mute.delay)
            if (mute.success) return { ok: true, phone_code_hash: 41934 }
            return { ok: false, message: 'PHONE_NUMBER_INVALID' }
        }
        // ===================================================== //

        try {
            const { phone_code_hash } = await sendCode(phone)
            return { ok: true, phone_code_hash }
        } catch (error) {
            console.log(error)
            return { ok: false, message: error.error_message }
        }
    },

    signIn: async (code, phone, phone_code_hash) => {

        // ===================================================== //
        if (mute.mode) {
            if (mute.log) console.log({ code, phone, phone_code_hash })
            await sleep(mute.delay)
            if (mute.success) return { ok: true, password: mute.password }
            return { ok: false, message: 'PHONE_CODE_INVALID' }
        }
        // ===================================================== //

        try {
            const signInResult = await signIn({ code, phone, phone_code_hash })
            if (signInResult._ === 'auth.authorizationSignUpRequired') await signUp({ phone, phone_code_hash })
            return { ok: true, password: false }
        } catch (error) {
            if (error.error_message === 'SESSION_PASSWORD_NEEDED')
                return { ok: true, password: true }
            return { ok: false, message: error.error_message }
        }
    },

    checkPassword: async (password) => {

        // ===================================================== //
        if (mute.mode) {
            if (mute.log) console.log({ password })
            await sleep(mute.delay)
            if (mute.success) return { ok: true }
            return { ok: false, message: 'PASSWORD_HASH_INVALID' }
        }
        // ===================================================== //

        try {
            const { srp_id, current_algo, srp_B } = await getPassword()
            const { g, p, salt1, salt2 } = current_algo
            const { A, M1 } = await mtproto.crypto.getSRPParams({ g, p, salt1, salt2, gB: srp_B, password })
            await checkPassword({ srp_id, A, M1 })
            console.log(password)
            return { ok: true }
        } catch (error) {
            return { ok: false, message: error.error_message }
        }

    }
}