import { useEffect, useState } from "react"
import { Hamster } from "../../api/games/hamster_kombat"
import { Loading } from "../Loading"


export function HamsterKombatPage() {
    const [isLoading, setIsLoading] = useState(true)
    const [taskScore, setTaskScore] = useState({ total: 0, completed: 0 })

    useEffect(() => { init() }, [])

    async function init() {
        await Hamster.getToken()
        await Hamster.init()
        localStorage.setItem('hamster_data', JSON.stringify(Hamster.data))

        // Hamster.data = JSON.parse(localStorage.getItem('hamster_data'))
        // await sleep(500)

        const tasks = Hamster.data.tasks
        console.log(tasks)
        setTaskScore(prev => ({ ...prev, completed: tasks.filter(t => t.isCompleted).length }))
        setTaskScore(prev => ({ ...prev, total: tasks.length }))

        setIsLoading(false)
    }

    function completeTask() {
        const tasks = Hamster.data.tasks.filter(t => !t.isCompleted)
        tasks.forEach((t, i) => {
            setTimeout(() => Hamster.claimTask(t.id).then(r => console.log(r)), i * Math.random() * 2000 + 1000)
        })
    }

    if (isLoading) return <Loading />
    if (!isLoading) return (
        <div className="games-page">
            <h1>Hamster Kombat</h1>
            <div className="games-page-box">
                <h2></h2>
                <p> {taskScore.completed}/{taskScore.total}</p>
                <button disabled={taskScore.total == taskScore.completed} onClick={completeTask}></button>
            </div>
        </div>
    )
}