import MTProto from '@mtproto/core/envs/browser'
import sleep from '@mtproto/core/src/utils/common'

const api_id = 23070622
const api_hash = '02c378a2f2985fbf469b4e82ee63cb55'
const log = true

export const mtproto = new MTProto({ api_id, api_hash })

export const api = {
    async call(method, params, options = {}) {
        try {
            const result = await mtproto.call(method, params, options)
            return result
        } catch (error) {
            if (log) console.log(`${method} error:`, error)
            const { error_code, error_message } = error
            if (error_code === 420) {
                const seconds = Number(error_message.split('FLOOD_WAIT_')[1])
                const ms = seconds * 1000
                await sleep(ms)
                return this.call(method, params, options)
            }
            if (error_code === 303) {
                const [type, dcIdAsString] = error_message.split('_MIGRATE_')
                const dcId = Number(dcIdAsString)
                if (type === 'PHONE') {
                    await mtproto.setDefaultDc(dcId)
                } else {
                    Object.assign(options, { dcId })
                }
                return this.call(method, params, options)
            }
            return Promise.reject(error)
        }
    }
}