import { api } from './api'

export async function getwebdata(username, url) {
    try {
        const resolvedPeer = await api.call('contacts.resolveUsername', { username })
        const peer = resolvedPeer.peer
        const user = resolvedPeer.users[0]
        const inputPeer = {
            _: 'inputPeerUser',
            user_id: peer.user_id,
            access_hash: user.access_hash
        }
        const inputUser = {
            _: 'inputUser',
            user_id: user.id,
            access_hash: user.access_hash
        }
        const webView = await api.call('messages.requestWebView', {
            peer: inputPeer,
            bot: inputUser,
            platform: 'android',
            from_bot_menu: false,
            url
        })
        return webView.url
    } catch (error) {
        console.log(error)
    }
}