import { createContext, useContext, useState } from 'react'
import { auth } from '../api/mtproto/auth'

const AuthContext = createContext()

export const useAuth = () => useContext(AuthContext)

export function AuthProvider({ children }) {
    const [authData, setAuthData] = useState({})
    const [isAuth, setIsAuth] = useState(false)
    const [user, setUser] = useState({})

    function checkAuth(callback) {
        auth.checkAuth().then(r => {
            if (r.ok == true) {
                callback(true)
                setIsAuth(true)
                setUser(r.user.users[0])
            } else {
                callback(false, r.message)
                setIsAuth(false)
                setUser({})
            }
        })
    }

    function sendCode(callback, phone) {
        auth.sendCode(phone).then(r => {
            if (r.ok) {
                callback(true)
                setAuthData(p => ({
                    ...p,
                    phone,
                    phone_code_hash: r.phone_code_hash
                }))
            } else
                callback(false, r.message)
        })
    }

    function signIn(callback, code) {
        auth.signIn(code, authData.phone, authData.phone_code_hash).then(r => {
            if (r.ok)
                callback(true, r.password)
            else
                callback(false, r.message)
        })
    }

    function checkPassword(callback, password) {
        auth.checkPassword(password).then(r => {
            console.log(r)
            if (r.ok)
                callback(true)
            else
                callback(false, r.message)
        })
    }

    return (
        <AuthContext.Provider value={{ authData, isAuth, user, checkAuth, sendCode, signIn, checkPassword }}>
            {children}
        </AuthContext.Provider>
    )
}