import { getwebdata } from "../mtproto/getwebdata"
import { sleep } from "../sleep"

const username = 'BlumCryptoBot'
const localStorageKey = 'blum_token'

export const Blum = {
    token: null,
    getToken: async function () {
        const _token = localStorage.getItem(localStorageKey)
        if (_token) return this.token = JSON.parse(_token)

        const url = await getwebdata(username, 'https://example.com/')
        const query = (decodeURIComponent(url.split('#tgWebAppData=')[1].split('&tgWebAppVersion')[0]))
        const response = await fetch('https://user-domain.blum.codes/api/v1/auth/provider/PROVIDER_TELEGRAM_MINI_APP', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ query })
        })

        const data = await response.json()
        const { access, refresh } = data.token
        const token = { access, refresh }
        localStorage.setItem(localStorageKey, JSON.stringify(token))
        this.token = token
    },
    refreshToken: async function () {
        const refresh = this.token.refresh
        const response = await fetch('https://user-domain.blum.codes/api/v1/auth/refresh', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ refresh })
        })
        const data = await response.json()
        this.token.access = data.access
    },
    playGame: async function () {
        const token = this.token.access
        const k = Math.random()
        const gameTime = (30 + k * 20)
        const points = Math.round(100 + k * 80)
        const gamePlayResponse = await fetch('https://game-domain.blum.codes/api/v1/game/play', {
            method: 'POST',
            headers: { 'Authorization': `Bearer ${token}` }
        })
        if (!gamePlayResponse.ok) return
        const gamePlayData = await gamePlayResponse.json()
        const gameId = gamePlayData.gameId
        alert(`play ${Math.round(gameTime)}s`)
        await sleep(gameTime * 1000)
        const gameClaimResponse = await fetch('https://game-domain.blum.codes/api/v1/game/claim', {
            method: 'POST',
            headers: { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' },
            body: JSON.stringify({ gameId, points })
        })
        if (!gameClaimResponse.ok) return
        alert(`claim +${points}`)
    }
}