import { useRef, useState } from 'react'
import { useAuth } from '../providers/AuthProvider'
import { BackButton } from './BackButton'

export function LoginPage() {
    const { sendCode, signIn, checkPassword } = useAuth()
    const [currentForm, setCurrentForm] = useState(0)
    const [alertText, setAlertText] = useState('')

    function alert(msg) {
        const ru = {
            PHONE_NUMBER_INVALID: '  ',
            PHONE_CODE_INVALID: '  ',
            PASSWORD_HASH_INVALID: ' '
        }
        setAlertText(ru[msg])
    }

    function back(ev) {
        ev.preventDefault()
        setCurrentForm(p => p - 1)
    }

    const callback1 = (phone, input, form) => {
        alert()
        sendCode((r, d) => {
            input.disabled = false
            form.reset()
            if (r) setCurrentForm(1)
            else alert(d)
        }, phone)
    }

    const callback2 = (code, input, form) => {
        alert()
        signIn((r, d) => {
            input.disabled = false
            form.reset()
            if (r) {
                if (d) setCurrentForm(2)
                else setCurrentForm(-1)
            } else alert(d)
        }, code)
    }

    const callback3 = (password, input, form) => {
        alert()
        checkPassword((r, d) => {
            input.disabled = false
            form.reset()
            if (r) setCurrentForm(-1)
            else alert(d)
        }, password)
    }

    const Forms = [
        <Form {...{
            currentForm, setCurrentForm, back,
            input: { type: 'text', placeholder: ' ' },
            callback: callback1
        }} />,
        <Form {...{
            currentForm, setCurrentForm, back,
            input: { type: 'text', placeholder: ' ' },
            callback: callback2
        }} />,
        <Form {...{
            currentForm, setCurrentForm, back,
            input: { type: 'password', placeholder: '' },
            callback: callback3
        }} />
    ]

    if (currentForm == -1) return window.location.reload()

    return (
        <div className='auth-form'>
            <h1 className='auth-header'>  Telegram</h1>
            {Forms[currentForm]}
            <div className='auth-alert'>{alertText}</div>
        </div>
    )
}



function Form(props) {
    const inputRef = useRef()

    function submitHandler(ev) {
        ev.preventDefault()
        inputRef.current.disabled = true
        props.callback(inputRef.current.value, inputRef.current, ev.target)
    }

    return (
        <form onSubmit={submitHandler}>
            <input ref={inputRef} {...props.input} />
            <button type='submit'></button>
            {props.currentForm > 0 && <BackButton onClick={props.back} />}
        </form>
    )
}